<template>
    <div class="list-wrapper">
        <yd-navbar  title="Agentily.com">

            <!-- <router-link to="/" slot="left">
                <yd-navbar-back-icon>Home</yd-navbar-back-icon>
            </router-link> -->

            <router-link to="/filter" slot="right">
                <span>Filter</span><p><img src="../../assets/icons/Filter.svg" alt=""></p>
            </router-link>

        </yd-navbar>

<!-- <p class="bigtitle">Appartments with 
    <span v-show="filters.bath_num > 0">{{filters.bath}} bath, </span> 
    <span v-show="filters.distric ">{{filters.distric}} distric, </span>
    <span v-show="filters.price_min ">{{filters.price_min}} - </span>
    <span v-show="filters.price_max ">{{filters.price_max}} RMB </span>
</p>
            -->

<el-row :gutter="10">
    <el-col class="item" :xs="12" :sm="12" :md="6" :lg="6" :xl="6" v-for="(o, index) in rooms" :key="index">
        <el-card :body-style="{ padding: '0px' }" class="cards">
            <router-link :to="'/details/' + o.id">
            <div class="image" v-for="item,index in o.images.slice(0,1)" :key='index'>
                            <img :src='item.image' />               
            </div>
            </router-link>
            <div style="padding: 0 10px 10px 10px;">
                <span style="font-size:18px">{{o.compound}}</span>
                <div class="bottom clearfix">
                <time class="time">Price：{{o.price}} </time>
                <time class="address">Address: {{o.address}} </time>
                <div class="time">{{o.rooms_num}} Bed 
                {{o.bath_num}} Bath</div>

                    <time class="time">Add time{{o.add_time}} </time>


                </div>
            </div>
        </el-card>
    </el-col>
</el-row>





  <div class="page">
    <span class="demonstration">Total:{{total}}</span>
    <el-pagination
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="10"
      layout="prev, pager, next"
      :total="total">
    </el-pagination>
  </div>

    </div>
</template>
<script>
import { APIGetroomlist } from '../../api/api'
import {mapGetters,mapState, mapMutations} from 'vuex'
import Vfilter from './filter'
export default {
    components:{
    Vfilter
    },
    data() {
        return {
            rooms: [],
            pic:'',
            currentPage: 4,
            total:0,
            totalpage:'',
        }
    },
    computed:{
        ...mapState(['filters']),
        ...mapMutations(['UPDATE_FILTER'])

    },
    methods:{

        getFilteredrooms() {
            console.log(this.filters)
            APIGetroomlist(
               this.filters
            ).then( res => { 
                // console.log(res.data)
                this.total = res.data.count
                
                this.rooms = res.data.results
                console.log(this.rooms)
                
            }).catch( err => { console.log( err )})
        },
           
        handleCurrentChange(value) {
            this.filters['p'] = value
            this.getFilteredrooms()
            // this.UPDATE_FILTER(this.filters)
        }
    },
    created () {
        this.getFilteredrooms()
    },
    activated(){ // 每次激活都执行的函数
        this.getFilteredrooms()
    }
    
}
</script>
<style lang="stylus" scoped>
.list-wrapper
    .cards
        margin-bottom 10px
    .page
        margin-top 20px
        .demonstration
            line-height 30px
    .item
        .image
            img
                width 100%


</style>






